@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
*{margin:0;padding:0;outline:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}html,body{color:#fff;background-color:#fff;display:grid;scroll-behavior:smooth;box-sizing:border-box;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif}a{text-decoration:none;color:#fff;cursor:pointer}.nav{background-color:rgba(59,127,230,0.945);box-shadow:-1px 1px 7px black;border-radius:5px;width:100%;text-align:center;position:fixed;font-size:0.8rem;grid-area:headerWrapper}.nav .nav-items{display:grid;grid-template-columns:auto;grid-template-rows:repeat(7, auto);grid-gap:3px;align-items:center}.nav .nav-items #menu-icon-inside{padding:50px;z-index:1;cursor:pointer;background-color:rgba(59,127,230,0.945);text-align:center;padding:5px;border-radius:50%;position:fixed;height:10px}.nav .nav-items .logo-wrapper{display:grid;justify-content:center;align-items:center;grid-template-columns:repeat(3, auto);grid-gap:20px}.nav .nav-items .logo-wrapper .logo-title{padding:5px;display:block;border-radius:3px;border:5px solid white}.nav .nav-items .logo-wrapper .logo-position{font-family:"Dancing Script",cursive;font-size:2em}.nav .nav-items a{padding:7px;transition:ease-in-out 300ms}.nav .nav-items a:hover{border-radius:5px;background-color:white;color:black}@media only screen and (min-width: 1042px){.nav .nav-items{grid-template-columns:auto auto auto auto auto auto auto;grid-template-rows:auto}}.main{text-align:center;width:100%;font-family:"Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;color:#2a2727;grid-area:mainWrapper}.main .intro{width:100%;height:auto}.main .title-box{padding-top:1rem;padding-left:3rem;padding-right:3rem}.main .title{font-family:"Lobster",cursive;font-style:italic;font-size:2rem;font-weight:bold;color:rgba(59,127,230,0.945);border-bottom:3px solid rgba(59,127,230,0.945);border-radius:3px;box-shadow:-1px 1px 7px black;padding:1rem 3rem}.main .top-intro .experiences{font-size:1rem;display:grid;grid-template-columns:repeat(auto-fill, minmax(400px, auto));padding:5px 50px 0px 50px;text-align:left;grid-gap:5px}.main .top-intro .exp{padding:30px;margin:10px 0px;border-radius:3px;box-shadow:0px 0px 5px grey}.main .top-intro .exp .exp-title{color:rgba(59,127,230,0.945);border-bottom:3px solid rgba(59,127,230,0.945)}.main .middle-intro .skills{font-size:1rem;display:grid;grid-template-columns:repeat(auto-fit, minmax(200px, auto));padding:50px;text-align:left;grid-gap:5px}.main .middle-intro .skill-box{padding:10px;margin:10px 0px;border-radius:3px;box-shadow:0px 0px 5px grey}.main .middle-intro .skill-box span{color:rgba(59,127,230,0.945)}.main .bottom-intro{display:grid;grid-template-columns:auto;grid-template-rows:auto}.main .bottom-intro .prog-langs{color:#fff;font-size:1rem;display:grid;grid-template-columns:repeat(auto-fit, minmax(100px, auto));padding:50px;text-align:left;grid-gap:5px}.main .bottom-intro .prog-lang-box{padding:5px 10px;margin:10px 0px;border-radius:3px;box-shadow:0px 0px 5px grey}.main .bottom-intro .education{color:black;font-size:1rem;display:grid;grid-template-columns:repeat(auto-fit, minmax(100px, auto));padding:50px;text-align:left;grid-gap:5px}.main .bottom-intro .edu-details{padding:30px;margin:10px 0px;border-radius:3px;box-shadow:0px 0px 5px grey}.main .projects{text-align:center;width:100%;margin-top:20px}.main .projects-grid{background-color:rgba(59,127,230,0.945);display:grid;margin:0 auto;grid-template-columns:repeat(auto-fit, minmax(320px, 1fr));padding:2rem 250px;grid-gap:20px;justify-content:center;align-items:center}.main .projects-grid p{color:white;font-size:0.89em;font-style:normal;font-weight:100;padding:10px}.main .projects-grid p::before{content:" < ";font-weight:900;font-size:17px;vertical-align:middle;color:#5d5d5d;transition:color 1s}.main .projects-grid p::after{content:" />";font-weight:900;font-style:oblique;font-size:17px;vertical-align:middle;color:#5d5d5d;transition:color 1s}.main .projects-grid p:hover::before,.main p:hover::after{color:darkorange}.main .projects-grid a{border-radius:6px;box-shadow:2px 0 2px 0px #56595d;box-sizing:border-box;background:#5d5d5d}.main .projects-grid img{height:350px;width:100%;object-fit:cover}.main .projects h2{width:800px;margin:auto auto 2% auto}.main .show-all-box{height:37px;display:block;background-color:rgba(59,127,230,0.945)}.main .show-all-box .show-all{background-color:#5d5d5d;padding:13px;border-radius:3px;transition:background-color 1s}.main .show-all-box .show-all:hover{background-color:#f13134}.footer-contact{display:grid;grid-template-columns:repeat(auto-fit, minmax(auto, auto));grid-template-rows:auto auto auto auto;grid-template-areas:"head" "detail" "links" "presonal-info";align-items:center;justify-items:center;background-color:#2a2727;width:100%;height:50vh;border-bottom:solid rgba(59,127,230,0.945) 3px;grid-area:footerWrapper}.footer-contact .footer-contact>h1{grid-area:head}.footer-contact .footer-contact>p{grid-area:detail}.footer-contact .heart{margin-left:10px;color:red}.footer-contact .contact-links{grid-area:links;font-size:25px}.footer-contact .contact-links #popup .popuptext{visibility:hidden;background-color:white;color:black;text-align:center;border-radius:6px;padding:15px;position:absolute;z-index:2;font-size:0.75rem}.footer-contact .contact-links #popup:hover .popuptext{visibility:visible}.footer-contact .contact-links .contact-details{padding:10px}.footer-contact .presonal-info{grid-area:presonal-info}.footer-contact .presonal-info span{padding:10px}

